////////////////////////////////////////////////////////////////////
// BLOCK SEE MORE

.see-more {

	.fade-info {
	    position: relative;
	    height: p(300);
	    overflow: hidden;
	    &:after {
	        content: '';
	        position: absolute;
	        left: 0; top: 75%; right: 0; bottom: 0;
	        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
	    }
	}

	&.active {
		.fade-info {
			height: auto;
			&:after {
				display: none;
			}
		}
		.action {
			display: none;
		}
	}

	.action {
	    position: relative;
	    text-align: left;
	    &:after {
	        content: '';
	        position: absolute;
	        height: 1px;
	        width: 100%;
	        top: 50%;
	        left: 0;
	        background-color: $color-border;
	    }

	    &:hover {
	        &:after {
	            // background-color: color(primary);
	        }
	    }

	    a {
	        position: relative;
	        z-index: 1;
	        font-size: p(13);
	        color: color(gray);
	        text-decoration: none;

	        display: inline-block;
	        padding: p(10) p(30);
	        border: 1px solid $color-border;
	        background-color: color(white);
			left: 50%;
			transform: translateX(-50%);
	        &:hover,
	        &:focus {
	            color: color(primary);
	            border-color: color(primary);
	        }
	    }
	}

}
